import moment from 'moment'
const useMoment = (createdAt) => {
  moment.updateLocale('zh-tw', {
    meridiemParse: /|||||/,
    meridiemHour: function (h, meridiem) {
      let hour = h
      if (hour === 12) {
        hour = 0
      }
      if (meridiem === '' || meridiem === '' || meridiem === '') {
        return hour
      } else if (meridiem === '' || meridiem === '') {
        return hour + 12
      } else {
        // ''
        return hour >= 11 ? hour : hour + 12
      }
    },
    meridiem: function (hour, minute) {
      const hm = hour * 100 + minute
      if (hm < 600) {
        return ''
      } else if (hm < 900) {
        return ''
      } else if (hm < 1130) {
        return ''
      } else if (hm < 1230) {
        return ''
      } else if (hm < 1800) {
        return ''
      } else {
        return ''
      }
    },
  })
  const date = new Date(createdAt)
  const timestamp = date.getTime()
  const fromNow = moment(timestamp).fromNow().replace('ago', '')
  let result = ''

  if (fromNow.includes('hour')) {
    if (fromNow.includes('an')) {
      result = '1 '
    } else {
      result = fromNow.replace('hours', '') + ' '
    }
  }


  if (fromNow.includes('minutes')) {
    result = '1 '
  }

  if (fromNow.includes('day')) {
    result = moment(timestamp).format('AHH:mm[]YYYY[]MM[]DD[]')
  }
  // an hour
  // minutes
  // days

  

  return result
}

export default useMoment
